import Scroll from "../../../base/scroll/scroll.vue"
import Upload from '../../../base/upload/upload.vue'
import Step from "../../../base/step/step.vue"
export default {
  name: "getPriceOneStep3",
  components: {
    Scroll,
    Upload,
    Step
  },
  data() {
    return {
      stepArr: ["", "", "", ""],
      stepIndex: 2,
      isOk: true, //
      chatImg: [] //
    }
  },
  created() {
    if (this.$route.query.rbObj) {
      this.chatImg = JSON.parse(this.$route.query.rbObj.customerServiceChartPicId)
    }
  },
  methods: {
    doNext() {
      if (this.chatImg.length === 0) {
        this.$vux.alert.show({
          title: '',
          content: '',
        });
        return false
      }
      let obj = {
        favImg: this.$route.query.obj.favImg,
        focusImg: this.$route.query.obj.focusImg,
        taoKeyArr: this.$route.query.obj.taoKeyArr,
        chatImg: this.chatImg
      }
      this.$router.push({ name: 'getPriceOneStep4', query: { buyerTaskRecordId: this.$route.query.buyerTaskRecordId, allow: this.$route.query.allow, obj: obj, rbObj: this.$route.query.rbObj } })
    }
  }
}



